import { Component, OnInit } from '@angular/core';

import { ToastrService } from 'ngx-toastr';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import { MultiDataSet, Label } from 'ng2-charts';

import { EmployeeService } from '../shared/employees.service';
import { ProjectService } from '../shared/projects.service';

import { Employee } from '../employee.model';
import { Project } from '../project.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  employeesList: Employee[];
  projectsList: Project[];
  designationArray = [];

  // Doughnut
  public doughnutChartLabels: Label[] = ['Mobile', 'Web'];
  public doughnutChartData: MultiDataSet = [];
  public doughnutChartType: ChartType = 'doughnut';

  // Bars
  public barChartOptions: any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels: Label[] = ['Project1', 'Project2', 'TestProject'];
  public barChartType: ChartType = 'bar';
  public barChartPlugins = [];

  public barChartData: ChartDataSets[] = [
    { data: [4, 3, 2], label: 'Employees Working' },
    { data: [8, 6, 4], label: 'Employees Needed' }

  ];

  constructor( private employeeService: EmployeeService,
               private projectService: ProjectService,
               private toastr: ToastrService

              ) { }

  ngOnInit() {
    this.getProjects();
    this.countProjects();
  }


  getProjects() {
    this.projectService.getProjects()
      .subscribe((projects) => {
        this.projectsList = projects;
        this.getEmployees();
      });
  }


  getEmployees() {
    this.employeeService.getEmployees()
      .subscribe((employees) => {
        this.employeesList = this.reArrangeEmpList(employees, this.projectsList, this.getProjectName);
      });
  }


  deleteEmployee(id) {
    this.employeeService.deleteEmployee(id)
      .subscribe((res: any) => {
        this.toastr.warning(res.message, 'Done!');
        this.ngOnInit();

      }, (errorRes) => {
        this.toastr.error(errorRes.error.message, 'Oops...');
      });
  }


  getProjectName(projectsList: Project[], projectId: string) {
    const selectedProject = projectsList.find( (project) => project._id === projectId);
    if (selectedProject) {
      return selectedProject.name;
    } else {
      return false;
    }
  }

  reArrangeEmpList(employeesList: Employee[], projectsList: Project[], getProjectName): Employee[] {
    employeesList.forEach((emp) => {
      if (emp.project === null) {
        emp.project = 'none';
      } else {
        const projectName = getProjectName(projectsList, emp.project);
        if (projectName) {
          emp.project = projectName;
        } else {
          emp.project = 'none';
        }
      }
    });

    return employeesList;
  }

  countProjects() {
    this.projectService.countProjects()
      .subscribe((designationArray: any) => {
        this.designationArray = designationArray;
        this.doughnutChartData = this.designationArray;

      }, (errorRes) => {
        this.toastr.error(errorRes.message, 'Oops...');
      });
  }


  employeeId(index, item) {
    return item._id;
  }

}
